import {message} from 'antd';
import {Button} from '@/panda-design';
import {Page, Grid} from '@/components';

export const MessagePage = () => {
    /* eslint-disable react/jsx-no-bind */
    return (
        <Page title="Message " status="">
            <Grid title="" repeat={4}>
                <Button onChange={() => message.info('')}></Button>
                <Button onChange={() => message.success('')}></Button>
                <Button onChange={() => message.error('')}></Button>
                <Button onChange={() => message.warning('')}></Button>
            </Grid>
        </Page>
    );
    /* eslint-enable react/jsx-no-bind */
};
